@import '/src/css/globales.scss';

.pokemonItem {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 290px;
    margin: 20px;
    padding: 20px;
    border-radius: 8px;
    background-color: $white;
    cursor: pointer;
    box-shadow: 1px 2px 3px 0px rgba(155, 155, 155, 0.882);
    transform: translateY(-0.1em);
    transition: transform 0.1s ease;


    &__back {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #f4f4f4d7;
        width: 120px;
        height: 120px;
        border-radius: 50%;
        margin-bottom: 10px;

        &__pokeImg {
            width: 170px;
            height: 170px;
            object-fit: contain;
        }
    }

    &__details {
        margin-top: 20px;
        text-align: center;

        h2 {
            font-size: 2.2rem;
        }

        p {
            font-size: 1.6rem;
        }
    }

    &__pokeball {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 26px;
        height: 26px;
        cursor: pointer;

        &__btn {
            width: 100%;
            height: 100%;
        }
    }
}

.pokemonItem:hover {
    transform: translateY(-0.3em);
}

.pokemonItem:active {
    transform: translateY(0);
}